


const PinCode = ({}) =>{

    return (
        <div className="pin-input-container">
            {[1,2,3,4].map((r)=>
                <PinInput index={r} />
            )}
        </div>
    )

}


export default PinCode;


const PinInput = ({index}) =>{
    return (
        <input 
            type="tel" 
            maxLength="1" 
            className="pin-number-input" 
            tabIndex={index} 
            dataUia={`pin-number-${index}`} 
            ariaLabel={`Entrada de PIN ${index}`} 
            value="" 
        />

           
    )

}





